export default function pagination<T>(pageNumber: string | undefined, valuesArray: T[]) {
    if (pageNumber === '' || pageNumber === undefined) {
        return valuesArray
    }


    const parsedPageNumber = Number.parseInt(pageNumber, 10)

    if (isNaN(parsedPageNumber)) {
        return valuesArray.slice(0, 10)
    }

    return valuesArray.slice(
        (parsedPageNumber * 10), 
        (parsedPageNumber * 10) + 10    
    )
}